import React, {useState} from "react";
import {Button, FormControl, InputGroup, Form} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';

const Search = ({onSearchSend}) => {
    const [searchValue, setSearchValue] = useState({
        'field': 'title',
        'param': 'gte',
        'value': ''
    })

    return (
        <div className="Search">
            <InputGroup className="mb-3">
                <Form.Control as="select" defaultValue="Название">
                    <option key='title'>Название</option>
                    <option key='count'>Количество</option>
                    <option key='distance'>Расстояние</option>
                </Form.Control>
                <Form.Control as="select" defaultValue="Больше">
                    <option key='gte'>Больше</option>
                    <option key='lte'>Меньше</option>
                    <option key='iexact'>Равно</option>
                    <option key='icontains'>Содержит</option>
                </Form.Control>
                <FormControl
                    aria-describedby="basic-addon2"
                    value={searchValue}
                    onChange={(event) => {
                        setSearchValue({
                            field: searchValue.field,
                            param: searchValue.param,
                            value: event.target.value
                        })
                    }}
                />
                <InputGroup.Append>
                    <Button
                        variant="outline-secondary"
                        onClick={() => {
                            onSearchSend(searchValue)
                        }}
                    >
                        Найти
                    </Button>
                </InputGroup.Append>
            </InputGroup>
        </div>
    );
}

export default Search;
